<template>
  <div class="magnifier">
    <!-- 小图 -->
    <div
      class="small-box"
      @mouseover="handOver"
      @mousemove="handMove"
      @mouseout="handOut"
      :style="{
        width: configs.width + 'px',
        height: configs.height + 'px',
      }"
    >
      <img class="smallPic" :src="`${src}?x-oss-process=image/resize,l_836`" />
      <div
        class="magnifier-zoom"
        v-show="showMask"
        :style="{
          background: configs.maskColor,
          height: calcMaskWidth + 'px',
          width: calcMaskHeight + 'px',
          opacity: configs.maskOpacity,
          transform: transformMask
        }"
      ></div>
    </div>
    <!-- 大图, 注意误差 -->
    <div
      class="magnifier-layer"
      v-show="showMagnifier"
      :style="{
        width: configs.width + 'px',
        height: configs.height + 'px',
        left: configs.width + 10 + 'px'
      }"
    >
      <div
        class="big-box"
        :style="{
          width: bigWidth + 'px',
          height: bigHeight + 'px',
          left: moveLeft,
          top: moveTop
        }"
      >
        <div
          class="big-box-img"
          :style="{
            width: bigWidth + 'px',
            height: bigHeight + 'px'
          }"
        >
          <img
            :src="bigSrc"
            :style="{
              width: bigWidth + 'px',
              height: bigHeight + 'px'
            }"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  // 小图地址
  props: {
    src: {
      type: String
    },
    // 大图地址
    bigSrc: {
      type: String
    },
    // 配置项
    configs: {
      type: Object,
      default () {
        return {
          width: 400, // 放大区域
          height: 400, // 放大区域
          maskWidth: 200, // 遮罩
          maskHeight: 200, // 遮罩
          maskColor: 'rgba(25,122,255,0.5)', // 遮罩样式
          maskOpacity: 0.6,
          scale: 2
        }
      }
    },
    autoScale: { // 自动处理缩放，优先级高
      type: Boolean,
      default: false
    },
    autoMask: { // 自动处理遮罩
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      imgObj: {},
      moveLeft: 0,
      moveTop: 0,
      transformMask: 'translate(0px, 0px)',
      showMagnifier: false,
      showMask: false,
      init: false
    }
  },
  computed: {
    bigWidth () {
      return this.calcScale * this.configs.width
    },
    bigHeight () {
      return this.calcScale * this.configs.height
    },
    calcScale () {
      if (this.autoScale) {
        return this.configs.width / this.configs.maskWidth
      }
      return this.configs.scale
    },
    calcMaskWidth () {
      if (this.autoScale || !this.autoMask) {
        return this.configs.maskWidth
      }
      return this.configs.width / this.configs.scale
    },
    calcMaskHeight () {
      if (this.autoScale || !this.autoMask) {
        return this.configs.maskHeight
      }
      return this.configs.height / this.configs.scale
    }
  },
  methods: {
    handMove (e) {
      // 动态获取小图的位置（或者监听 scroll ）
      const imgRectNow = this.imgObj.getBoundingClientRect()
      const objX = e.clientX - imgRectNow.left
      const objY = e.clientY - imgRectNow.top
      // 计算初始的遮罩左上角的坐标
      let maskX = objX - this.calcMaskWidth / 2
      let maskY = objY - this.calcMaskHeight / 2

      // 判断是否超出界限,并纠正
      maskY = maskY < 0 ? 0 : maskY
      maskX = maskX < 0 ? 0 : maskX
      if (maskY + this.calcMaskHeight >= imgRectNow.height) {
        maskY = imgRectNow.height - this.calcMaskHeight
      }
      if (maskX + this.calcMaskWidth >= imgRectNow.width) {
        maskX = imgRectNow.width - this.calcMaskWidth
      }

      // 遮罩移动
      this.transformMask = `translate(${maskX}px, ${maskY}px)`

      // 背景图移动
      this.moveLeft = -maskX * this.calcScale + 'px'
      this.moveTop = -maskY * this.calcScale + 'px'
    },
    handOut () {
      this.showMagnifier = false
      this.showMask = false
    },
    handOver () {
      if (!this.init) {
        this.init = true
        this.imgObj = this.$el.getElementsByClassName('small-box')[0]
      }
      this.showMagnifier = true
      this.showMask = true
    }
  }
}
</script>
<style lang="scss" scoped>
.magnifier {
  position: relative;
  .small-box {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    .smallPic {
      width: 100%;
    }
    .magnifier-zoom {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  .magnifier-layer {
    z-index: 9999;
    position: absolute;
    overflow: hidden;
    border: 1px solid $color_border_gray;
    top: -1px;
    .big-box {
      position: absolute;
      overflow: hidden;
      .big-box-img {
        position: relative;
        overflow: hidden;
      }
    }
  }
}
</style>
