<template>
  <div class="carousel">
    <el-button
      class="arrow left-arrow"
      icon="el-icon-arrow-left"
      @click="left"
    ></el-button>
    <div class="item-list-container" ref="container">
      <div class="item-list" :style="{ marginLeft: curPos + 'px' }">
        <div
          v-for="(item, index) in list"
          :key="index"
          class="item"
          :class="{ active: active == index }"
          :style="{ width: itemWidth + 'px' }"
        >
          <slot v-if="isSlot" :item="item" :index="index"></slot>
          <img v-else :src="item.url" @mouseenter="onClick(item, index)" />
        </div>
      </div>
    </div>
    <el-button
      class="arrow right-arrow"
      icon="el-icon-arrow-right"
      @click="right"
    ></el-button>
  </div>
</template>
<script>
export default {
  name: 'Carousel',
  props: {
    list: {
      type: Array,
      default: () => []
    },
    showCount: {
      type: Number,
      default: 5
    },
    isSlot: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      active: 0,
      curIndex: 0,
      curPos: 0,
      itemWidth: 100,
      move: 16,
      timer: null
    }
  },
  mounted () {
    if (this.$refs.container) {
      this.itemWidth = (this.$refs.container.offsetWidth - (this.showCount - 1) * 10) / this.showCount
    }
  },
  beforeDestroy () {
    if (this.timer) {
      clearInterval(this.timer)
      this.timer = null
    }
  },
  computed: {
    willPos () {
      return -(this.itemWidth + 10) * this.curIndex
    }
  },
  methods: {
    onClick (item, index) {
      this.active = index
      this.$emit('click', item, index)
    },
    left () {
      if (this.curIndex - 1 >= 0) {
        this.curIndex--
        this.setPosition()
      }
    },
    right () {
      if (this.curIndex + 1 <= this.list.length - 4) {
        this.curIndex++
        this.setPosition()
      }
    },
    // 添加位置更新定时器
    setPosition () {
      if (this.timer) {
        return
      }
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        if (this.positionUpdate()) {
          if (this.timer) {
            clearInterval(this.timer)
            this.timer = null
          }
        }
      }, 40)
    },
    // 位置更新,true更新完毕
    positionUpdate () {
      if (this.willPos !== this.curPos) {
        var offset = this.willPos - this.curPos
        var offsetSign = 1
        if (offset < 0) {
          offsetSign = -1
        }
        if (Math.abs(offset) < this.move) {
          this.curPos = this.willPos
          return true
        } else {
          this.curPos += this.move * offsetSign
        }
        return false
      }
      return true
    }
  }
}
</script>
<style lang="scss" scoped>
.carousel {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  .arrow {
    padding: 0;
    width: 20px;
    height: 80%;
    text-align: center;
    border: 1px solid $color_border_gray;
  }
  .item-list-container {
    flex: 1;
    margin-left: 10px;
    margin-right: 10px;
    height: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: left;
    .item-list {
      display: flex;
      justify-content: left;
      align-items: center;
      .item {
        margin-right: 10px;
        flex-shrink: 0;
        flex-grow: 0;
        cursor: pointer;
        box-sizing: border-box;
        border: 1px solid transparent;
        &.active {
          border: 1px solid $color_primary;
        }
        &:last-child {
          margin-right: 0;
        }
        img {
          width: 100%;
        }
      }
    }
  }
}
</style>
