<template>
  <div class="search">
    <div class="layout content">
      <div class="search-form">
        <div class="line check">
          <div class="label">
            <span>分类</span>
          </div>
          <div class="value">
            <div
              class="checkBox"
              :class="{ active: form.type.indexOf(item.gid) > -1 }"
              v-for="item in list"
              :key="item.gid"
              @click="clickType(item.gid)"
            >
              <span>{{ item.name }}</span>
            </div>
          </div>
        </div>
        <div class="line keySearch">
          <div class="label">
            <span>关键词</span>
          </div>
          <div class="value">
            <el-input v-model="form.key"></el-input>
          </div>
        </div>
        <div class="btn-div">
          <el-button type="primary" @click="getList">开始查找</el-button>
        </div>
      </div>
      <div class="goods-list">
        <el-row :gutter="20">
          <el-col
            class="goods-div"
            :span="8"
            v-for="item in result"
            :key="item.gid"
          >
            <div class="goods" @click="toGoodsDetails(item)">
              <div class="img-div">
                <img :src="item.url" alt="" />
              </div>
              <div class="goods-desc">{{ item.title }}</div>
            </div>
          </el-col>
        </el-row>
        <div style="width: 100%;">
          <pagination
            :total="total"
            :page.sync="form.page"
            :limit.sync="form.pageSize"
            @pagination="getList"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
