    return {
      list: [
        {
          gid: '1',
          name: 'T恤'
        },
        {
          gid: '2',
          name: 'T恤：长袖'
        },
        {
          gid: '3',
          name: 'T恤'
        },
        {
          gid: '4',
          name: '三分裤'
        },
        {
          gid: '5',
          name: '五分裤'
        },
        {
          gid: '6',
          name: '长裤'
        },
        {
          gid: '7',
          name: '七分裤'
        },
        {
          gid: '8',
          name: '八分裤'
        },
