  methods: {
    clickType (value) {
      var index = this.form.type.indexOf(value)
      if (index > -1) {
        this.form.type.splice(index, 1)
      } else {
        this.form.type.push(value)
      }
    },
    onSearch () {
      console.log('search', this.form)
    },
    getList () {},
    // 去商品详情页
    toGoodsDetails (goods) {
      this.$router.push({ path: '/details', query: { gid: goods.gid } })
    }
  }
}
