<style lang="scss" scoped>
.search {
  padding: 20px;
  .content {
    .search-form {
      padding: 20px;
      background-color: $color_bg_white;
      .keySearch {
        padding-top: $padding;
        width: 100%;
        border-top: 1px dashed $color_border_gray;
        border-bottom: 1px dashed $color_border_gray;
      }
      .btn-div {
        margin-top: $margin;
        text-align: center;
      }
    }
    .goods-list {
      margin-top: 20px;
      padding: 0 1px 0 0;
      position: relative;
      display: flex;
      flex-wrap: wrap;
      .goods-div {
        margin-bottom: 20px;
        .goods {
          height: auto;
          overflow: hidden;
          font-size: 0;
          position: relative;
          background-color: $color_bg_white;
          cursor: pointer;
          .img-div {
            position: relative;
            overflow: hidden;
            min-height: 200px;
            display: flex;
            align-items: center;
            img {
              width: 100%;
              height: auto;
              transform: scale(1);
              transition: transform 0.5s;
              &:hover {
                transform: scale(1.1);
                transition: transform 0.5s;
              }
            }
          }

          .goods-desc {
            font-size: $font_size_normal;
            line-height: 40px;
            color: $color_text;
            height: 60px;
            width: 100%;
            padding-left: 10px;
            &:hover {
              color: $color_primary;
            }
          }
        }
      }
    }
  }
}
</style>
