<template>
  <div class="details">
    <div class="layout content">
      <div class="left">
        <div class="show">
          <div class="img-magnifier">
            <Magnifier :src="imgSrc" :bigSrc="bigImgSrc" />
          </div>
          <div class="show-list">
            <Carousel :list="details.imgList" @click="showClick"></Carousel>
          </div>
          <div class="love-container">
            <div class="love">收藏商品 （1175人气）</div>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="title">{{ details.title }}</div>
        <div v-if="details.subtitle" class="subtitle">
          {{ details.subtitle }}
        </div>
        <div class="price-div">
          <div class="line">
            <div class="label">价格</div>
            <div class="value price">￥{{ details.price }}</div>
          </div>
        </div>
        <div class="startNumber">
          <div class="line">
            <div class="label">起批量</div>
            <div class="value">{{ details.startNumber }}件</div>
          </div>
        </div>
        <div class="sizes">
          <div class="line check">
            <div class="label">尺码</div>
            <div class="value">
              <div
                class="checkBox"
                :class="{active: form.size == item}"
                v-for="item in details.sizes"
                :key="item"
                @click="clickSize(item)"
              >
                <span>{{ item }}</span>
                <i class="tick"></i>
              </div>
            </div>
          </div>
        </div>
        <div class="colors">
          <div class="line check">
            <div class="label">颜色</div>
            <div class="value">
              <div
                class="checkBox checkImg color"
                :class="{active: form.color == item.label}"
                v-for="item in details.colors"
                :key="item.label"
                @click="clickColor(item)"
              >
                <img width="100%" :src="item.url" alt="" />
              </div>
            </div>
          </div>
        </div>
        <div class="count">
          <div class="line">
            <div class="label">数量</div>
            <div class="value">
              <el-input-number
                v-model="form.count"
                :min="1"
                label="数量"
                size="small"
              ></el-input-number>
            </div>
            <div class="tip" style="margin-left: 20px;">
              库存25件
            </div>
          </div>
        </div>
        <div class="line btns">
          <div class="label"></div>
          <div class="value">
            <el-button type="danger" plain>立即购买</el-button>
            <el-button type="danger">加入购物车</el-button>
            <el-button type="danger" icon="el-icon-download"
              >产品详情</el-button
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Magnifier from '@/components/common/Magnifier'
import Carousel from '@/components/common/Carousel'
export default {
