        subtitle: '刺绣印花装饰 下摆系带设计 舒适纯棉面料',
        price: '99.50',
        startNumber: 10,
        colors: [
          {
            label: '白',
            url: require('@/assets/images/200.jpg')
          }
        ],
        sizes: ['M', 'L', 'XL'],
        imgList: [
          {
            id: '7030',
            index: '7030',
            title: '彪马情侣短T',
            url: require('@/assets/images/200.jpg')
          },
          {
            id: '7031',
            index: '7031',
            title: '彪马情侣短T(1)',
            url: require('@/assets/images/200(1).jpg')
          },
          {
            id: '7032',
            index: '7032',
            title: '彪马情侣短T',
            url: require('@/assets/images/200.jpg')
          },
          {
            id: '7033',
            index: '7033',
            title: '彪马情侣短T(1)',
            url: require('@/assets/images/200(1).jpg')
          },
          {
            id: '7033',
            index: '7033',
            title: '彪马情侣短T(1)',
            url: require('@/assets/images/200(1).jpg')
          },
          {
            id: '7033',
            index: '7033',
            title: '彪马情侣短T(1)',
            url: require('@/assets/images/200(1).jpg')
          },
          {
            id: '7033',
            index: '7033',
            title: '彪马情侣短T(1)',
            url: require('@/assets/images/200(1).jpg')
          },
          {
            id: '7033',
            index: '7033',
