            url: require('@/assets/images/200(1).jpg')
          }
        ]
      },
      imgSrc: require('@/assets/images/200.jpg'),
      bigImgSrc: require('@/assets/images/200.jpg'),
      form: {
        size: '',
        color: '',
        count: 1
      }
    }
  },
  methods: {
    showClick (item, index) {
      this.imgSrc = item.url
      this.bigImgSrc = item.url
    },
    // 点击尺码
    clickSize (item) {
      this.form.size = item
    },
    // 点击颜色
    clickColor (item) {
      this.form.color = item.label
    }
  }
}
</script>
<style lang="scss" scoped>
.details {
  .content {
    display: flex;
    padding-top: 40px;
    padding-bottom: 40px;
    .left {
      width: 400px;
      margin-right: 10px;
      .show {
      }
      .show-list {
        height: 100px;
        width: 100%;
      }
      .love-container {
        line-height: 30px;
        color: $color_gray;
      }
    }
    .right {
